import React from "react";

export default class Table extends React.Component {

    FormatTitle = (string) => {
        const title = string.charAt(0).toUpperCase() + string.slice(1);
        return string.replace(/([A-Z])/g, ' $1').trim();
    }

    render() {
        const {
            Title,
            Array
        } = this.props,
            keyNames = [];

        Array.map((row) => {
            const allKeys = Object.keys(row);
            allKeys.forEach(key => {
                switch(true) {
                    case (key === "given"):
                    case (key === "family"):
                    case (key === "period"):
                    case (key === "reference"):
                    case (typeof key === 'interger'):
                    case (keyNames.includes(key)):
                        return 
                    default:
                        return keyNames.push(key);
                }
            });
        });

        return <table>
            <thead>
                {Title ? (
                    <tr>
                        <th>{this.FormatTitle(Title)}</th>
                    </tr>
                ) : null}
                <tr>
                    {keyNames.map((key, i) => {
                        if (!isNaN(key)) return <th key={i}>{`${parseInt(key) + 1}.)`}</th>
                        return <th key={i}>{this.FormatTitle(key)}</th>
                    })}
                </tr>
            </thead>
            <tbody>
                {Array.map((row, index) => {
                    return <tr key={index}>
                        {keyNames.map((key, i) => {
                            switch(true) {
                                case (key === "relationship"):
                                case (key === "name"):
                                    return <td key={i}>{row[key][0].text}</td>
                                case (typeof row[key] === "string"):
                                    return <td key={i}>{row[key]}</td>
                                case (typeof row[key] === "array"):
                                    return <td key={i}>
                                        <Table
                                            Array={row[key]}
                                        />
                                    </td>
                                case (typeof row[key] === "object"):
                                    return <td key={i}>
                                            <Table
                                                Array={[row[key]]}
                                            />
                                        </td>
                                default:
                                    return
                            }
                        }
                        )}
                    </tr>
                })}
            </tbody>
        </table>
    }
}